// assets/<item>/scss/sprite.entry.scss

// SCSS do plugin tạo ra (đừng sửa file generated)
@import './sprite.generated';

// Luôn tạo mixin riêng để tránh đụng với template có sẵn
@mixin ms-sprites($sprites) {
  @each $sprite in $sprites {
    $name: if(type-of($sprite) == 'map', map-get($sprite, 'name'), nth($sprite, 10));
    .MS__sprite-#{$name} {
      @include sprite($sprite);
      display: block;
    }
  }
}

// Xuất toàn bộ class prefix MS__sprite-
@include ms-sprites($spritesheet-sprites);